import { Component, OnInit } from '@angular/core';
import { ApiService } from '../services/api.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-statistics',
  templateUrl: './statistics.component.html',
  styleUrls: ['./statistics.component.css']
})
export class StatisticsComponent implements OnInit {
  players: any[] = []; // Lista de jugadores
  statistics: any[] = []; // Lista de estadísticas
  statisticForm: FormGroup; // Formulario reactivo

  constructor(private apiService: ApiService, private fb: FormBuilder) { }

  ngOnInit(): void {
    this.getPlayers(); // Cargar jugadores al inicio
    this.getStatistics(); // Cargar estadísticas al inicio
    this.initForm(); // Inicializar el formulario reactivo
  }

  // Inicializa el formulario con las validaciones
  initForm(): void {
    this.statisticForm = this.fb.group({
      player: ['', Validators.required], // Jugador (obligatorio)
      match: ['', Validators.required],  // Partido (obligatorio)
      goals: [0, [Validators.required, Validators.min(0)]], // Goles (mínimo 0)
      assists: [0, [Validators.required, Validators.min(0)]], // Asistencias (mínimo 0)
      minutesPlayed: [0, [Validators.required, Validators.min(0)]], // Minutos jugados (mínimo 0)
    });
  }

  // Cargar jugadores desde el API
  getPlayers(): void {
    this.apiService.getPlayers().subscribe(
      (data) => {
        this.players = data;
      },
      (error) => {
        console.error('Error al obtener jugadores:', error);
      }
    );
  }

  // Método llamado cuando se cambia de jugador
  onPlayerChange(event: Event): void {
    const value = (event.target as HTMLSelectElement).value;
    console.log('Jugador seleccionado:', value);
    console.log('Lista de jugadores:', this.players);
  }

  // Enviar el formulario para crear una nueva estadística
  onSubmit(): void {
    const formValue = this.statisticForm.value;

    // Buscar el jugador seleccionado por nombre (o usar el ID directamente)
    const selectedPlayer = this.players.find(player => player.name === formValue.player);
    
    if (!selectedPlayer) {
      console.error('Jugador no encontrado');
      return;
    }

    // Preparar el payload con playerId y otros datos del formulario
    const payload = {
      playerId: selectedPlayer.id,  // Usamos el ID del jugador
      match: formValue.match,       // Partido
      goals: formValue.goals,       // Goles
      assists: formValue.assists,   // Asistencias
      minutesPlayed: formValue.minutesPlayed  // Minutos jugados
    };

    console.log('Payload enviado:', payload);

    // Enviar el payload al backend
    this.apiService.createPlayer( payload).subscribe({
      next: response => {
        console.log('Estadística guardada correctamente', response);
        this.getStatistics(); // Recargar las estadísticas después de guardar
        this.clearForm(); // Limpiar el formulario
      },
      error: err => {
        console.error('Error al guardar estadística', err);
      }
    });
  }

  // Cargar estadísticas desde el API
  getStatistics(): void {
    this.apiService.getStatistics().subscribe(
      (data) => {
        this.statistics = data;
      },
      (error) => {
        console.error('Error al obtener estadísticas:', error);
      }
    );
  }

  // Limpiar el formulario
  clearForm(): void {
    this.statisticForm.reset();
  }

  // Eliminar una estadística por ID
  deleteStatisticById(id: number): void {
    this.apiService.deleteStatistic(id).subscribe(
      () => {
        this.statistics = this.statistics.filter(stat => stat.id !== id);
      },
      (error) => {
        console.error('Error al eliminar estadística:', error);
      }
    );
  }

  // Actualizar una estadística por ID
  updateStatisticById(id: number): void {
    if (this.statisticForm.invalid) {
      console.error('Formulario inválido');
      return;
    }

    // Solo actualizar los campos modificados
    const updatedStatisticData = this.statisticForm.value;

    this.apiService.updateStatistic(id, updatedStatisticData).subscribe(
      (statistic) => {
        const index = this.statistics.findIndex(stat => stat.id === id);
        if (index !== -1) {
          this.statistics[index] = statistic; // Actualizar la estadística
        }
      },
      (error) => {
        console.error('Error al actualizar estadística:', error);
      }
    );
  }
}
