import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { catchError, map } from 'rxjs/operators';


@Injectable({
  providedIn: 'root'
})
export class ApiService {
  private baseUrl = 'https://tmpro-5265168c0988.herokuapp.com/api';

  constructor(private http: HttpClient) {}

  // ** Equipos **
  getAllTeams(): Observable<any[]> {
    return this.http.get<any[]>(`${this.baseUrl}/teams`).pipe(
      map(response => Array.isArray(response) ? response : []),
      catchError(error => {
        console.error('Error al obtener equipos:', error);
        return of([]); // Retorna un array vacío en caso de error
      })
    );
  }
  

  // ** Roles **
getRoles(): Observable<any[]> {
  return this.http.get<any[]>(`${this.baseUrl}/roles`).pipe(
    map(response => Array.isArray(response) ? response : []),
    catchError(error => {
      console.error('Error al obtener roles:', error);
      return of([]);  // Retorna un array vacío en caso de error
    })
  );
}

// Obtener las estadísticas de los jugadores
getStatistics(): Observable<any[]> {
  return this.http.get<any[]>(`${this.baseUrl}/statistics`).pipe(
    catchError(error => {
      console.error('Error al obtener estadísticas:', error);
      return of([]);  // Retorna un array vacío en caso de error
    })
  );
}

// Agregar una nueva estadística
createStatistic(statistic: any): Observable<any> {
  return this.http.post<any>(`${this.baseUrl}/statistics`, statistic);
}

// Eliminar una estadística
deleteStatistic(statisticId: number): Observable<any> {
  return this.http.delete<any>(`${this.baseUrl}/statistics/${statisticId}`);
}
getStatisticsByPlayerId(playerId: number): Observable<any[]> { 
  return this.http.get<any[]>(`${this.baseUrl}/statistics/player/${playerId}`).pipe( 
    catchError(error =>
       { console.error('Error al obtener estadísticas del jugador:', error); return of([]);  }) ); }

       updateStatistic(id: number, statistic: any): Observable<any> { return this.http.put<any>(`${this.baseUrl}/statistics/${id}`, statistic); }

  createTeam(team: any): Observable<any> {
    return this.http.post<any>(`${this.baseUrl}/teams`, team);
  }

  updateTeam(id: number, team: any): Observable<any> {
    return this.http.put<any>(`${this.baseUrl}/teams/${id}`, team);
  }

  deleteTeam(id: number): Observable<any> {
    return this.http.delete<any>(`${this.baseUrl}/teams/${id}`);
  }

  // ** Jugadores **
  getPlayers(): Observable<any[]> {
    return this.http.get<any[]>(`${this.baseUrl}/players`).pipe(
      map(response => Array.isArray(response) ? response : []),
      catchError(error => {
        console.error('Error al obtener jugadores:', error);
        return of([]); // Retorna un array vacío en caso de error
      })
    );
  }

  createPlayer(player: any): Observable<any> {
    console.log('Datos enviados para crear jugador:', player);  // Verifica los datos antes de enviarlos
    return this.http.post<any>(`${this.baseUrl}/players`, player);
  }
  

  updatePlayer(id: number, player: any): Observable<any> {
    console.log('Datos enviados para crear jugador:', player);  // Verifica los datos antes de enviarlos
    return this.http.put<any>(`${this.baseUrl}/players/${id}`, player);
  }

  deletePlayer(id: number): Observable<any> {
    return this.http.delete<any>(`${this.baseUrl}/players/${id}`, );
  }

  registerUser(user: { username: string; password: string; roleId: number }): Observable<any> {
    return this.http.post(`${this.baseUrl}/auth/register`, user).pipe(
        catchError(error => {
            console.error('Error al registrar usuario:', error);
            return of(error.error);  // Manejo de errores
        })
    );
}

  



login(username: string, password: string): Observable<any> {
  const body = { password, username };  // Asegúrate de que la API reciba estos campos correctamente
  return this.http.post(`${this.baseUrl}/auth/login`, body,);
}

  

  // ** Logout **
  logout(): Observable<any> {
    return this.http.post(`${this.baseUrl}/auth/logout`, {}, );
  }
}
